import React from 'react';
import PageHeader from 'components/common/PageHeader';
import { Card } from 'react-bootstrap';

const Changelog = () => (
  <>
    <PageHeader title="Changelog" className="mb-3" />

    <Card className="mb-3">
      <Card.Header>
        <h5>
          <code className="fw-bold fs-1"> v3.0.0-alpha1</code> - initial Release
        </h5>
        <p className="mb-0">30 September, 2019</p>
      </Card.Header>
      <Card.Body className="bg-light">Nothing to see here.</Card.Body>
    </Card>
  </>
);

export default Changelog;
